import React from 'react';
import styled from 'styled-components';
import ProfileContents from './ProfileContents';
import ProfileFooter from './ProfileFooter';
import selfImage from '../../images/profile.jpg';
import BoxFade from '../BoxFade';

const ProfileContainer = styled.div`
  position: relative;
  width: 100%;
  height: calc(100% - 10rem);
  display: flex;
  align-self: flex-start;
  align-items: center;
  justify-content: space-around;
  padding: 0 2rem 0 6rem;

  @media only screen and (max-width: 1280px) {
    padding: 2rem 0;
  }
`;

const ImageTemplate = styled.div`
  width: 30rem;
  height: 40rem;
  border: 0.2rem dashed ${(props) => props.color};
  border-radius: 50%;
  background: url(${selfImage}) center no-repeat;
  background-size: cover;
  animation: ${BoxFade} 1s forwards;
  z-index: 2;

  @media only screen and (max-width: 1280px) {
    position: absolute;
    right: 12rem;
    top: 2rem;
    width: 20rem;
    height: 20rem;
  }

  @media only screen and (max-width: 1024px) {
    width: 15rem;
    height: 15rem;
  }
`;

const ContentTemplate = styled.div`
  display: flex;
  flex-direction: column;
  width: 70rem;
  height: 100%;
  padding: 2rem;
  opacity: 0;
  animation: ${BoxFade} 1s 0.5s forwards;

  @media only screen and (max-width: 1280px) {
    width: 100%;
  }
`;

const ContentTitle = styled.div`
  padding-bottom: 1rem;

  h2 {
    font-size: 3.6rem;
  }

  div {
    opacity: 0.5;
    font-size: 2.4rem;
  }

  @media only screen and (max-width: 1024px) {
    h2 {
      font-size: 2.4rem;
    }

    div {
      font-size: 1.8rem;
    }
  }
`;

const ContentIntroduction = styled.div`
  margin: 1rem 0;
  padding: 1rem 0 1rem 0.5rem;
  font-size: 1.8rem;
  border-top: 0.1rem solid ${(props) => props.color};
  border-bottom: 0.1rem solid ${(props) => props.color};
  display: flex;
  align-items: center;

  ul {
    margin: 0;
    padding: 0;
    list-style: none;

    li {
      line-height: 3.2rem;
      display: flex;
      align-items: center;

      strong {
        margin: 0.2rem;
        font-weight: 600;
        line-height: 1.8rem;
        padding: 0.2rem;
        border: 0.2rem solid gray;
        border-radius: 0.5rem;
        background: ${(props) => props.color};
        color: white;
      }

      &:nth-child(3),
      &:nth-child(5) {
        margin-left: 0.3rem;
      }
    }
  }

  @media only screen and (max-width: 1024px) {
    font-size: 1.4rem;
  }
`;

const ContentWrap = styled.div``;

function Profile(props) {
  const profileItem = [
    {
      title: 'Skills',
      content: 'React.js / JavaScript / HTML / CSS / Sass / Python / Node.js',
    },
    {
      title: `Education`,
      content: ' IT',
    },
    {
      title: 'Email',
      content: 'sesabc1005@naver.com',
    },
    {
      title: 'Phone',
      content: '010-2338-6240',
    },
    {
      title: 'Motto',
      content: '    . /    .',
    },
  ];

  const renderProfileContents = (arr) => {
    return arr.map((item, index) => {
      return (
        <ProfileContents
          key={index}
          color={props.color}
          title={item.title}
          content={item.content}
        />
      );
    });
  };

  return (
    <>
      <ProfileContainer>
        <ImageTemplate color={props.color} />
        <ContentTemplate>
          <ContentTitle color={props.color}>
            <h2>DaengKIM |  </h2>
            <div>Front-end Web Developer</div>
          </ContentTitle>
          <ContentIntroduction color={props.color}>
            <ul>
              <li>
                <strong>  </strong>    
                  <strong></strong>.
              </li>
              <li>
                <strong>   , </strong>
                 .
              </li>
              <li>
                ,   <strong></strong>  .
              </li>
              <li>
                <strong></strong>   {' '}
                <strong></strong>  .
              </li>
              <li>
                     <strong> </strong>{' '}
                .
              </li>
            </ul>
          </ContentIntroduction>
          <ContentWrap>{renderProfileContents(profileItem)}</ContentWrap>
        </ContentTemplate>
      </ProfileContainer>
      <ProfileFooter color={props.color} />
    </>
  );
}

export default React.memo(Profile);
